// components/home/HomeMain.tsx
"use client";

import React, { useEffect, useState } from "react";
import { useAuth } from "@/contexts/AuthContext";
import { supabase } from "@/lib/supabase";
import BottomBar from "./BottomBar";
import FloatingActionButton from "./FloatingActionButton";

import { motion } from "framer-motion";
import ProgressCircles from "./ProgressCircles";
import MealCard from "./MealCard";
import { ChevronRight } from "lucide-react";

type MealPlanItem = {
  id?: string;
  title: string;
  calories?: number;
  protein?: number;
  carbs?: number;
  fat?: number;
  description?: string;
  eaten?: boolean;
  generatedByAI?: boolean;
};

export default function HomeMain() {
  const { user } = useAuth();
  const [loading, setLoading] = useState(true);
  const [todayMeals, setTodayMeals] = useState<MealPlanItem[]>([]);
  const [shoppingPreview, setShoppingPreview] = useState<string[]>([]);
  const [dailyStats, setDailyStats] = useState({
    waterPercent: 0,
    mealsPercent: 0,
    caloriesPercent: 0,
    calories: 0,
    caloriesGoal: 2000,
  });

  useEffect(() => {
    (async () => {
      setLoading(true);
      try {
        // 1) buscar plano gerado pela IA (ai_generated_plans) — se existir use o dia hoje
        if (!user) {
          setLoading(false);
          return;
        }

        const { data: aiPlans } = await supabase
          .from("ai_generated_plans")
          .select("id, user_id, plan_type, content, created_at")
          .eq("user_id", user.id)
          .order("created_at", { ascending: false })
          .limit(1);

        if (aiPlans && aiPlans.length > 0) {
          const content = aiPlans[0].content as any;
          // Esperamos content.weekly: { "2025-11-25": [ {title, calories,...}, ... ] }
          const todayKey = new Date().toISOString().slice(0, 10);
          const todayArray =
            content?.weekly?.[todayKey] ?? content?.today ?? null;
          if (Array.isArray(todayArray)) {
            setTodayMeals(
              todayArray.map((m: any, i: number) => ({
                id: m.id ?? `ai-${i}`,
                title: m.title,
                calories: m.calories,
                protein: m.protein,
                carbs: m.carbs,
                fat: m.fat,
                description: m.description,
                eaten: false,
                generatedByAI: true,
              }))
            );
          }
          // preview de lista de compras
          setShoppingPreview(content?.shopping?.slice(0, 6) ?? []);
        }

        // 2) fallback: se não houver plano IA, tentar carregar localStorage (quiz_result_meta or quiz_plan)
        const local =
          typeof window !== "undefined"
            ? localStorage.getItem("ai_plan")
            : null;
        if (!aiPlans?.length && local) {
          try {
            const parsed = JSON.parse(local);
            const todayArray =
              parsed?.weekly?.[new Date().toISOString().slice(0, 10)] ??
              parsed?.today;
            if (Array.isArray(todayArray)) {
              setTodayMeals(
                todayArray.map((m: any, i: number) => ({
                  id: m.id ?? `local-${i}`,
                  title: m.title,
                  calories: m.calories,
                  protein: m.protein,
                  carbs: m.carbs,
                  fat: m.fat,
                  description: m.description,
                  eaten: false,
                  generatedByAI: true,
                }))
              );
            }
            setShoppingPreview(parsed?.shopping?.slice(0, 6) ?? []);
          } catch (e) {
            // ignore
          }
        }

        // 3) obter estatísticas básicas (pode derivar de weight_records / water_records / meals)
        // simplificado: buscar total calorias de hoje registrados (caso exista tabela meals)
        const { data: mealsToday } = await supabase
          .from("meals")
          .select("id, user_id, type, description, calories")
          .eq("user_id", user.id)
          .gte("date", new Date().toISOString().slice(0, 10))
          .lte("date", new Date().toISOString().slice(0, 10));

        const caloriesSum = Array.isArray(mealsToday)
          ? mealsToday.reduce((s: number, m: any) => s + (m.calories ?? 0), 0)
          : 0;

        setDailyStats((s) => ({ ...s, calories: caloriesSum }));
      } catch (err) {
        console.error("Erro ao carregar home:", err);
      } finally {
        setLoading(false);
      }
    })();
  }, [user]);

  const handleToggleEaten = (index: number) => {
    setTodayMeals((prev) =>
      prev.map((m, i) => (i === index ? { ...m, eaten: !m.eaten } : m))
    );

    // atualizar porcentagem de refeições
    setDailyStats((s) => {
      const eaten =
        todayMeals.filter((tm) => tm.eaten).length +
        (todayMeals[index]?.eaten ? -1 : 1);
      const percent = Math.round(
        (eaten / Math.max(1, todayMeals.length)) * 100
      );
      return { ...s, mealsPercent: percent };
    });
  };

  if (!user) {
    return (
      <div className="min-h-screen flex items-center justify-center">
        <div className="text-center">
          <h2 className="text-xl font-semibold">Bem-vindo ao Auraline</h2>
          <p className="text-sm text-gray-500">
            Faça login para ver seu plano.
          </p>
        </div>
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-white pb-28">
      <div className="max-w-3xl mx-auto px-5 pt-6">
        {/* HEADER */}
        <div className="flex items-center justify-between">
          <div className="flex items-center gap-4">
            <div className="w-12 h-12 rounded-full bg-gray-100 flex items-center justify-center text-lg font-semibold text-gray-700">
              {user?.user_metadata?.full_name?.[0] ?? user?.email?.[0] ?? "U"}
            </div>
            <div>
              <div className="text-sm text-gray-500">Bom dia,</div>
              <div className="text-xl font-bold text-[#0A0A0A]">
                {user?.user_metadata?.full_name ?? user?.email}
              </div>
            </div>
          </div>

          <div
            className="flex items-center gap-1 text-green-600 cursor-pointer"
            onClick={() => router.push("/progresso")}
          >
            <ChevronRight size={20} strokeWidth={2} />
          </div>
        </div>

        {/* CIRCULOS DE PROGRESSO */}
        <div className="mt-6">
          <ProgressCircles
            waterPercent={dailyStats.waterPercent}
            mealsPercent={dailyStats.mealsPercent}
            caloriesPercent={Math.min(
              100,
              Math.round((dailyStats.calories / dailyStats.caloriesGoal) * 100)
            )}
            calories={dailyStats.calories}
            caloriesGoal={dailyStats.caloriesGoal}
          />
        </div>

        {/* REFEIÇÕES DO DIA */}
        <div className="mt-6 space-y-4">
          <div className="flex items-center justify-between">
            <h3 className="text-lg font-semibold">Refeições de hoje</h3>
            <span className="text-sm text-gray-500">
              {new Date().toLocaleDateString()}
            </span>
          </div>

          <div className="space-y-3">
            {loading && <div className="text-gray-400">Carregando...</div>}
            {!loading && todayMeals.length === 0 && (
              <div className="text-gray-500">
                Nenhuma refeição encontrada para hoje — a IA ainda não gerou um
                plano.
              </div>
            )}
            {!loading &&
              todayMeals.map((m, i) => (
                <MealCard
                  key={m.id ?? i}
                  meal={m}
                  onToggle={() => handleToggleEaten(i)}
                />
              ))}
          </div>
        </div>

        {/* PLANO DA SEMANA (preview pequeno) */}
        <motion.div className="mt-6 p-4 rounded-2xl bg-white shadow-[0_6px_20px_rgba(0,0,0,0.04)]">
          <div className="flex items-center justify-between">
            <div>
              <div className="text-sm text-gray-500">Plano da semana</div>
              <div className="text-base font-semibold">
                Sua sugestão personalizada
              </div>
            </div>
            <button className="text-sm text-[#00C974]">
              Ver cardápio completo
            </button>
          </div>
          <div className="mt-3 text-sm text-gray-600">
            Visualize o plano semanal gerado pela IA — toque no dia para ver
            detalhes.
          </div>
        </motion.div>

        {/* LISTA DE COMPRAS PREVIEW */}
        <div className="mt-6 flex gap-4 items-center justify-between">
          <div className="flex-1 p-4 rounded-2xl bg-white shadow-[0_6px_20px_rgba(0,0,0,0.04)]">
            <div className="flex items-center justify-between">
              <div>
                <div className="text-sm text-gray-500">Lista de compras</div>
                <div className="text-base font-semibold">Itens sugeridos</div>
              </div>
              <button className="text-sm text-[#00C974]">Abrir lista</button>
            </div>

            <div className="mt-3 text-sm text-gray-600">
              {shoppingPreview.length === 0 ? (
                <span className="text-gray-400">Nenhum item gerado ainda</span>
              ) : (
                <ul className="space-y-1">
                  {shoppingPreview.map((s, i) => (
                    <li key={i} className="text-sm text-gray-700">
                      • {s}
                    </li>
                  ))}
                </ul>
              )}
            </div>
          </div>

          {/* BLOCO PEQUENO DE STATUS */}
          <div className="w-40 p-4 rounded-2xl bg-white shadow-[0_6px_20px_rgba(0,0,0,0.04)]">
            <div className="text-sm text-gray-500">Calorias</div>
            <div className="text-lg font-semibold text-[#0A0A0A]">
              {dailyStats.calories} kcal
            </div>
            <div className="text-xs text-gray-400 mt-1">
              Meta: {dailyStats.caloriesGoal} kcal
            </div>
          </div>
        </div>
      </div>

      {/* FAB + BottomBar */}
      <FloatingActionButton />
      <BottomBar />
    </div>
  );
}
